import {
  ChangeDetectorRef,
  Component,
  ElementRef,
  EventEmitter,
  Host,
  Input,
  OnChanges,
  OnDestroy,
  OnInit,
  Optional,
  Output,
  ViewChild,
  ViewEncapsulation
} from '@angular/core';
import { ControlValueAccessor, FormControl, NgControl } from '@angular/forms';
import { FormComponent } from '@sofico-framework/ui-kit/components/form';
import {
  OnSofFocus,
  SOF_FOCUS_COMPONENT
} from '@sofico-framework/ui-kit/directives/focus';
import { SearchFnSelect, SearchOption } from '@sofico-framework/ui-kit/types';
import {
  NzFilterOptionType,
  NzSelectItemInterface,
  NzSelectSizeType
} from 'ng-zorro-antd/select';
import { Changes, takeUntilDestroy, UntilDestroy } from 'ngx-reactivetoolkit';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';

@UntilDestroy()
@Component({
  selector: 'sof-input-single-select',
  styleUrls: ['./input-single-select.component.scss'],
  encapsulation: ViewEncapsulation.None,
  template: `
    <div class="single-select">
      <nz-select
        #inputElement
        [@.disabled]="true"
        [formControl]="internalFormControl"
        [nzSize]="size"
        [nzShowSearch]="showSearch"
        [nzAllowClear]="clearable"
        [nzBorderless]="borderless"
        [nzDisabled]="isDisabled"
        [nzFilterOption]="nzSearchFn"
        [nzServerSearch]="serverSearch"
        [nzPlaceHolder]="placeholder"
        [nzNotFoundContent]="tc + notFoundContent | translate"
        [class.is-invalid]="
          invalid ||
          (ngControl?.invalid && (ngControl?.touched || form?.submitted))
        "
        (nzBlur)="onTouch()"
        (nzOnSearch)="changeSearch.emit($event)"
      >
        <ng-container *ngFor="let o of formattedOptions$ | async">
          <nz-option
            *ngIf="!searchLoading"
            [nzValue]="o.value"
            [nzLabel]="o.label"
            [nzDisabled]="o.disabled"
          ></nz-option>
        </ng-container>
        <nz-option *ngIf="searchLoading" nzDisabled nzCustomContent>
          <sof-loading size="sm"></sof-loading>
        </nz-option>
      </nz-select>
    </div>
  `,
  providers: [
    { provide: SOF_FOCUS_COMPONENT, useExisting: InputSingleSelectComponent }
  ]
})
export class InputSingleSelectComponent
  implements ControlValueAccessor, OnChanges, OnInit, OnDestroy, OnSofFocus {
  @Input() tc: string;

  // Size of Select input
  @Input() size: NzSelectSizeType = 'large';

  // Whether to show the search icon
  @Input() showSearch = true;

  // Whether to show the clear button icon
  @Input() clearable = true;

  // Whether the select has borderless styling
  @Input() borderless = false;

  // Whether the search is loading in case of a server based search
  @Input() searchLoading = false;

  // Whether the search will be done server side.
  // This will disable the filtering done on the options since we would expect
  // the options to be filtered already on server side.
  @Input() serverSearch = false;

  // Specify content to show when no result matches..
  @Input() notFoundContent = '.NOT-FOUND';

  // The placeholder of the input.
  @Input() placeholder = '';

  /**
   * The id of the input to connect to a label tag.
   * currently not supported
   */
  @Input() labelForId: string;

  /**
   *  Determines if the input is disabled.
   */
  @Input() isDisabled: boolean;

  /**
   * Determines which property that must be used as list label.
   */
  @Input() selectorLabel: (x: any) => any;

  /**
   * Determines which property that must be used as list value.
   */
  @Input() selectorValue: (x: any) => any;

  /**
   * Determines which property that must be used as option disable.
   */
  @Input() selectorDisabled: (x: any) => any;

  /**
   * The options that populate the list.
   */
  @Input() options: any[];

  /**
   * Determines whether the input is in a valid state.
   */
  @Input() invalid = false;

  /**
   * EventEmitter that will emit the value when changed.
   */
  @Output() changeValue = new EventEmitter<any>();

  /**
   * EventEmitter that will emit the full object value when changed
   */
  @Output() changeObjectValue = new EventEmitter<any>();

  /**
   * EventEmitter that will emit the current search term
   */
  @Output() changeSearch = new EventEmitter<string>();

  /**
   * EventEmitter that will emit when control is touched.
   */
  @Output() touch = new EventEmitter<any>();

  // Form control used instead in simple value, as the ng-select component has internal implementation we can't reach.
  // By making use of a form control internally we have all the features available.
  internalFormControl = new FormControl(null);
  propagateChange: any;
  propagateTouch: any;

  // source streams
  @Changes('options') options$: Observable<any[]>;

  // presentation streams
  formattedOptions$: Observable<
    { label: string; value: any; disabled: boolean }[]
  >;

  @ViewChild('inputElement', { read: ElementRef }) inputElement: ElementRef;

  // Determines how the search is done.
  @Input() searchFn: SearchFnSelect = (input: string, x: SearchOption) =>
    x?.label?.toLowerCase().startsWith(input.toLowerCase());

  nzSearchFn: NzFilterOptionType = (input: string, x: NzSelectItemInterface) =>
    this.searchFn(input, { label: x.nzLabel as string, value: x.nzValue });

  constructor(
    @Optional() public form: FormComponent,
    @Optional() @Host() public ngControl: NgControl,
    private changeDetectorRef: ChangeDetectorRef
  ) {
    if (ngControl) {
      ngControl.valueAccessor = this;
    }
  }

  sofFocus(): void {
    this.inputElement.nativeElement.getElementsByTagName('input')[0].focus();
    this.changeDetectorRef.detectChanges();
  }

  ngOnInit(): void {
    this.formattedOptions$ = this.options$.pipe(
      map(options => options ?? []),
      map(options =>
        options.map(option => ({
          label: this.selectorLabel(option),
          value: this.selectorValue(option),
          disabled: this.selectorDisabled ? this.selectorDisabled(option) : null
        }))
      )
    );

    // the reason why we're using valueChanges instead of fn ngModelChange
    // is because:
    // When we want to update the single-select without that the user
    // did something we will trigger the fn writeValue. That fn
    // will update the form value.
    // When we update the value we're aware of the change of the value.
    // So sometimes we don't want that valueChanges isn't triggered.
    // that's possible by adding { emitEvent: false } to the patchValue
    // but when you use the fn ngModelChange he will ignore the
    // { emitEvent: false } option. So to use that we need to
    // listen on the valueChanges events instead.
    this.internalFormControl.valueChanges
      .pipe(takeUntilDestroy(this))
      .subscribe(value => {
        if (!this.isDisabled) {
          this.sofFocus();
          this.changeValue.emit(value);
          const fullObject = this.options.find(
            option => this.selectorValue(option) === value
          );
          this.changeObjectValue.emit(fullObject);

          if (this.propagateChange) {
            this.propagateChange(value);
          }
        }
      });
  }

  ngOnChanges(): void {}

  ngOnDestroy(): void {
    if (this.ngControl?.valueAccessor) {
      // Every time a control is re-created the previous writeValue reference(s) is not cleaned up.
      // So, over time, a lot of these references can be built up. This memory leak is a bug in Angular's implementation of ControlValueAccessor.
      // We hide that problem by assigning an empty function to writeValue every time we destroy the control.
      // An detailed explanation of the problem can be found here: https://github.com/angular/angular/pull/29335
      // The bug issue for it: https://github.com/angular/angular/issues/20007
      this.ngControl.valueAccessor.writeValue = () => {};
    }
  }

  registerOnChange(fn: any): void {
    this.propagateChange = fn;
  }

  registerOnTouched(fn: any): void {
    this.propagateTouch = fn;
  }

  writeValue(value: any): void {
    this.internalFormControl.setValue(value ?? null, { emitEvent: false });
  }

  onTouch(): void {
    this.touch.emit();

    if (!this.isDisabled && this.propagateTouch) {
      this.propagateTouch();
    }
  }

  setDisabledState(value: boolean): void {
    this.isDisabled = value;
  }
}
